                <p><input type="password" id="pwd_box" placeholder="" v-model="pwd"></p>
                <span class="btn_cancel div_btn" @click="tobtn('cancel')"></span>
                <span class="btn_login div_btn" @click="tobtn('login')"></span>
            </form>
