<script>
import { Message } from "element-ui";
export default {
  data() {
    return {
      flag: true,
      usertype: "",
      user: "",
      pwd: "",
      loginflag: false
    };
  },
  methods: {
    removelogin() {
      let login_box = document.querySelector(".login_box");
      if (login_box && this.loginflag) {
        login_box.style.display = "none";
      }
    },
    listlogin() {
      let login_list = document.querySelector(".login");
      if (this.flag) {
        login_list.style.display = "block";
        this.flag = false;
      } else {
        login_list.style.display = "none";
        this.flag = true;
      }
    },
    login(v) {
      let login_box = document.querySelector(".login_box");
      login_box.style.display = "block";
      this.usertype = v;
    },
    tobtn(v) {
      let that = this;
      if (v === "cancel") {
        let login_box = document.querySelector(".login_box");
        login_box.style.display = "none";
      } else {
        // 
        if(!this.pwd.match(/^[a-zA-Z]\w{5,17}$/)){
            Message.error("6~18");
        }
        //  
        // 0  fac
        // 1 
        // 2 
        fetch("http://api.test.dajiuxing.com.cn/rescue/user/login", {
          method: "POST",
          body: `username=${this.user}&pwd=${this.pwd}`,
          mode: "cors",
          headers: { "Content-Type": "application/x-www-form-urlencoded" }
        })
          .then(function(res) {
            return res.json();
          })
          .then(function(data) {
            if (data.code === 0) {
              if (data.obj.user.type === 0) {
                Message({
                  message: "",
                  type: "success"
                });
                window.localStorage.setItem(
                  "data",
                  JSON.stringify({
                    data: data.obj.token
                  })
                );
                window.localStorage.setItem(
                  "insti",
                  JSON.stringify(data.obj.user.insti)
                );
                that.$router.push({
                  path: "/fac",
                  name: "Fac",
                  params: {
                    token: data.obj.token,
                    insti: data.obj.user.insti
                  }
                });
              }
            } else if (data.code === 200026) {
              //   alert(",");
              Message.error("");
            } else {
              Message.error("");
              //   alert(",");
            }
            console.log(data);
          });
      }
    }
  },
  mounted() {}
};
</script>
