<template>
    <div class="form_box">
        <el-table
        :data="tableData"
        border>
            <el-table-column
                prop="number"
                label=""
                width="50">
            </el-table-column>
            <el-table-column
                prop="casenumber"
                label=""
                width="70">
            </el-table-column>
            <el-table-column
                prop="address"
                label=""
                width="70">
            </el-table-column>
            <el-table-column
                prop="username"
                label=""
                width="60">
            </el-table-column>
            <el-table-column
                prop="phone"
                label=""
                width="70">
            </el-table-column>
            <el-table-column
                prop="papers"
                label=""
                width="90">
            </el-table-column>
            <el-table-column
                prop="sex"
                label=""
                width="50">
            </el-table-column>
            <el-table-column
                prop="time"
                label=""
                width="90">
            </el-table-column>
            <el-table-column
                prop="par"
                label=""
                width="90">
                <template slot-scope="scope">
                   <el-button  type="text" size="small" v-show="scope.row.isshow"  @click="toUrl(scope.row.insuranceUrl)"></el-button>  
                    <el-button  type="text" size="small" v-show="!scope.row.isshow"></el-button> 
                </template>
            </el-table-column>
            <el-table-column
                prop="plan"
                label=""
                width="60">
            </el-table-column>
            <el-table-column
                prop="node"
                label=""
                width="60">
            </el-table-column>
            <el-table-column
                prop="get_time"
                label=""
                width="90">
            </el-table-column>
            <el-table-column
                prop="op"
                label=""
                width="100">
                <template slot-scope="scope">
                    <el-button  type="text" size="small" @click="topar(scope.$index)"></el-button>
                </template>
            </el-table-column>
        </el-table>
    </div>
</template>

<script>
export default {
  props: ["token",'st_time','en_time'],
  data() {
    return {
      tableData: [],
      obj: [],
      victimList: []
    };
  },
  methods: {
    topar(index) {
      let that = this;
      console.log(that.obj);
      this.$router.push({
        path: "par",
        name: "Par",
        params: {
          token: that.token,
          obj: that.obj,
          victimList: that.victimList,
          index
        }
      });
    },
    time(str) {
      var date = new Date(str); //10*1000131000
      var Y = date.getFullYear() + "";
      var M =
        (date.getMonth() + 1 < 10
          ? "0" + (date.getMonth() + 1)
          : date.getMonth() + 1) + "";
      var D = date.getDate() + "";
      var h = date.getHours() + ":";
      var m = date.getMinutes() + ":";
      var s = date.getSeconds();
      return Y + M + D + h + m + s;
    },
    toUrl(url) {
      window.open(url);
    }
  },
  mounted() {
    let that = this;
    let n = 0;
    let sex = "";
    let isshow = false;
    console.log(this.st_time)
    fetch("http://api.test.dajiuxing.com.cn/rescue/case/list_case", {
      method: "POST",
      body: `token=${this.token ||
        JSON.parse(window.localStorage.getItem("tokon"))
          .obj}&typeId=1&status=140`,
      mode: "cors",
      headers: { "Content-Type": "application/x-www-form-urlencoded" }
    })
      .then(function(res) {
        console.log('await');
        return res.json();
      })
      .then(function(data) {
        console.log(data);
        if (data.obj) {
          data.obj.map(v => {
            if (!v.solutionState) {
              n = n + 1;
              if (v.victimList[0].gender == 1) {
                sex = "";
              } else {
                sex = "";
              }
              if (
                v.victimList[0].obj.insurancePaper &&
                v.victimList[0].obj.insurancePaper != ""
              ) {
                isshow = true;
              } else {
                isshow = false;
              }
              that.tableData.push({
                number: n, //
                casenumber: v.obj.caseNo, //
                address: v.obj.locId, //
                username: v.victimList[0].obj.name, //
                phone: v.victimList[0].obj.contact, //
                papers: v.victimList[0].obj.idNo, //
                sex: sex, //
                time: that.time(v.obj.incidentTs), //
                par: " ", //
                plan: " ",
                node: " ",
                get_time: that.time(v.obj.incidentTs),
                op: ""
              });
              console.log(v.obj);
              that.obj.push(v.obj);
              that.victimList.push(v.victimList[0]);
            }
          });
        }
      });
  }
};
</script>

<style scoped>
</style>


